import { Component, OnInit, OnChanges, SimpleChanges, ChangeDetectorRef } from '@angular/core';

declare var Quagga: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  public lastResult = "No Changes";
  public config= {
    inputStream: {
        size: 800
    },
    locator: {
        patchSize: "medium",
        halfSample: false
    },
    numOfWorkers: 1,
    decoder: {
        readers: [{
            format: "code_128_reader",
            config: {}
        }]
    },
    locate: true,
    src: null
};

  constructor(private ref: ChangeDetectorRef) { }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['lastResult']) {
      console.log(this.lastResult);
    }
  }

  ngOnInit() {
  
  }

  handleInputChange(e) {
    console.log("e=",e);
    if (e.target.files && e.target.files.length >0) {
      this.decode(URL.createObjectURL(e.target.files[0]));
  }

}

decode(src){
  console.log("src=",src);
  this.config.src = src;
  Quagga.decodeSingle(this.config, function(result) {});
  Quagga.onProcessed((result) => this.onProcessed(result));
  Quagga.onDetected((result) => this.onDetected(result));
}

  onProcessed(result: any) {
    console.log("barcodeOnProcessed=",result);
    var drawingCtx = Quagga.canvas.ctx.overlay,
    drawingCanvas = Quagga.canvas.dom.overlay;

    if (result) {
      if (result.boxes) {
        drawingCtx.clearRect(0, 0, parseInt(drawingCanvas.getAttribute("width")), parseInt(drawingCanvas.getAttribute("height")));
        result.boxes.filter(function (box) {
          return box !== result.box;
        }).forEach(function (box) {
          Quagga.ImageDebug.drawPath(box, {x: 0, y: 1}, drawingCtx, {color: "green", lineWidth: 2});
        });
      }

      if (result.box) {
        Quagga.ImageDebug.drawPath(result.box, {x: 0, y: 1}, drawingCtx, {color: "#00F", lineWidth: 2});
      }

      if (result.codeResult && result.codeResult.code) {
        Quagga.ImageDebug.drawPath(result.line, {x: 'x', y: 'y'}, drawingCtx, {color: 'red', lineWidth: 3});
      }
    }
  }

  onDetected(result) {
    console.log("barcode=",result);
    this.lastResult = result.codeResult.code;
    
    this.ref.detectChanges();
  }
}


